.header{
    background-color: #003580;
    color: #fff;
    display: flex;
    justify-content: center;
    position: relative;
}
.headerContainer{
    width: 100%;
    max-width: 1024px;
    margin: 20px 0px 100px 0px;
}
.headerContainer.listMode{
    margin: 20px 0px 50px 0px;
}
.headerList{
    display: flex;
    gap: 40px;
}
.headerListItem{
    display: flex;
    align-items: center;
    gap: 10px;
}
.headerListItem.active{
    border: 1px solid #fff;
    padding: 10px;
    border-radius: 20px;
}
.headerDesc{
    margin: 20px 0px;
}
.headerBtn{
    background-color: #0071c2;
    cursor: pointer;
    border: none;
    padding: 10px;
    font-weight: 500;
    color: #fff;
}
.headerSearch{
    height: 30px;
    background-color: #fff;
    border: 3px solid #febb02;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 10px 0;
    border-radius: 5px;
    position: absolute;
    bottom: -25px;
    width: 100%;
    max-width: 1024px;
}
.headerIcon{
    color: lightgray;
}
.headerSearchItem{
    display: flex;
    align-items: center;
    gap: 10px;
}
.headerSearchInput{
    border: none;
    outline: none;
}
.headerSearchText{
    color: lightgray;
}
.date{
    position: absolute;
    top: 50px;
    z-index: 2;
}
.options{
    z-index: 2;
    position: absolute;
    top: 50px;
    color: gray;
    background-color: #fff;
    border-radius: 5px;
    -webkit-box-shadow:0px 0px 10px -5px rgba(0,0,0,0.4);
    box-shadow: 0px 0px 10px -5px rgba(0,0,0,0.4);
}
.optionItem{
    display: flex;
    justify-content: space-between;
    width: 200px;
    margin: 10px;
}
.optionCounter{
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 12px;
    color: #000;
}
.optionCounterButton{
    width: 30px;
    height: 30px;
    border:1px solid #0071c2;
    color: #0071c2;
    cursor: pointer;
    background-color: inherit;
}
.optionCounterButton:disabled{
    cursor: not-allowed;
}