.pList{
    width: 100%;
    max-width: 1024px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
}
.pListItem{
    flex: 1;
    overflow: hidden;
    border-radius: 10px;
    cursor: pointer;
}
.pListImg{
    width: 100%;
    height: 150px;
    object-fit: cover;
}
.pListTitles>h1{
    font-size: 18px;
    color: #555;
    text-transform: capitalize;
}
.pListTitles>h2{
    font-size: 14px;
    font-weight: 500;
    text-transform: capitalize;
}